.App {
  text-align: center;
  font-size: 14px;
  background-color: #282c34;
}

.container {
  min-height: 80vh;
  display: flex; /* Flexbox를 사용합니다 */
  flex-direction: row; /* 방향을 row로 변경합니다 */
  align-items: center; /* 가운데 정렬 */
  justify-content: center; /* 항목들 사이에 균일한 간격을 만듭니다 */
  font-size: calc(8px + 1.5vmin);
  color: white;
  padding: 0 5%; /* 좌우 패딩을 추가하여 내용이 화면 가장자리에 붙지 않게 합니다 */
}

table {
  border-collapse: collapse;
  margin: 20px 0;
  width: 210px;
  table-layout: fixed;
}

th,
td {
  border: 1px solid #ddd;
  padding: 4px 8px; /* 패딩 줄임 */
  font-size: calc(7px + 1vmin); /* 폰트 사이즈 자동 조절 */
  text-align: left;
  white-space: nowrap; /* 텍스트가 테이블 셀을 넘어가지 않도록 합니다. */
  display: flex; /* 플렉스 박스로 설정 */
  align-items: center; /* 세로로 중앙 정렬 */
  justify-content: flex-start; /* 수평으로 왼쪽 정렬 */
}

th {
  background-color: #4caf50;
  color: white;
}

tr:nth-child(even) {
  background-color: #313131;
}

tr:hover {
  background-color: #ddd;
}

table {
  border-radius: 5px;
  overflow: hidden;
}

th,
td {
  border-radius: 5px;
}

img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 10px; /* 이미지와 이름 사이에 간격 추가 */
}

button {
  margin: 0 10px; /* 버튼 위아래 margin 추가 */
  padding: 10px 20px;
  font-size: calc(8px + 1.5vmin); /* 버튼 글자 크기 자동 조절 */
  text-align: center;
  border: none;
  border-radius: 5px;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button span {
  display: block;
  line-height: 1.2;
}

button:hover {
  background-color: #45a049;
}
